.footer {
    &__content {
      display: flex;
      flex-direction: column;
      color: #fff;
  
      @include onSmallDesktop {
        flex-direction: row;
        gap: 98px;
        flex-wrap: wrap;
      }
  
      @include onDesktop {
        gap: 0;
      }
    }
  
    &__message {
      @include onTablet {
        max-width: 362px;
      }
    }
  
    &__message-title {
      font-weight: 600;
      font-size: 32px;
      line-height: 42px;
  
      margin-bottom: 48px;
  
      @include onTablet {
        margin-bottom: 56px;
      }
    }
  
    &__contacts {
      margin-bottom: 85px;
  
      @include onTablet {
        margin-bottom: 140px;
      }
  
      @include onDesktop {
        margin-left: 225px;
        margin-bottom: 188px;
      }
    }
  
    &__logo {
      display: block;
      margin-bottom: 48px;
      width: 46px;
      height: 28px;
  
      @include hover(transform, scale(1.2));
  
      @include onTablet {
        margin-bottom: 24px;
      }
    }
  
    &__bot {
      @include onSmallDesktop {
        display: flex;
        gap: 200px;
      }
  
      @include onDesktop {
        gap: 450px;
      }
    }
  }
  
  .form {
    &__input {
      width: 100%;
      display: block;
      font-family: inherit;
  
      font-weight: 600;
      font-size: 15px;
      line-height: 22px;
      padding-bottom: 16px;
      color: #fff;
  
      border: transparent;
      border-bottom: 1px solid #fff;
      background: none;
  
      transition: all 0.5s;
  
      margin-bottom: 40px;
  
      @include onTablet {
        margin-bottom: 48px;
      }
  
      &::placeholder {
        opacity: 0.7;
      }
  
      &:focus {
        border-color: #144ed4;
        outline: none;
      }
    }
  
    &__textarea {
      margin-bottom: 56px;
  
      @include onTablet {
        margin-bottom: 80px;
      }
    }
  
    &__btn {
      margin-bottom: 80px;
  
      @include onTablet {
        margin-bottom: 120px;
      }
  
      @include onDesktop {
        margin-bottom: 0;
      }
    }
  }
  
  .contacts {
    &__item {
      margin-bottom: 40px;
  
      &:last-child {
        margin-bottom: 0;
      }
  
      @include onTablet {
        margin-bottom: 48px;
  
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  
    &__title {
      font-weight: 600;
      font-size: 32px;
      line-height: 48px;
  
      margin-bottom: 48px;
  
      @include onTablet {
        margin-bottom: 56px;
      }
    }
  
    &__item-title {
      font-weight: 700;
      font-size: 13px;
      line-height: 20px;
  
      letter-spacing: 3px;
      text-transform: uppercase;
  
      opacity: 0.5;
  
      margin-bottom: 8px;
    }
  
    &__item-link {
      font-family: $fontOpenSans;
      font-weight: 600;
      font-size: 24px;
      line-height: 36px;
  
      color: #fff;
  
      @include hover(color, #2060f6);
    }
  
    &__item-socials {
      display: flex;
      gap: 36px;
      padding-top: 8px;
    }
  
    &__item-social {
      height: 32px;
      width: 32px;
  
      background-repeat: no-repeat;
      background-position: center;
  
      transition: all 0.5s;
    }
  
    &__item-facebook {
      background-image: url(../images/facebook.svg);
  
      &:hover {
        background-image: url(../images/facebook-blue.svg);
        transform: scale(1.2);
      }
    }
  
    &__item-twitter {
      background-image: url(../images/twitter.svg);
  
      &:hover {
        background-image: url(../images/twitter-blue.svg);
        transform: scale(1.2);
      }
    }
  
    &__item-instagram {
      background-image: url(../images/instagram.svg);
  
      &:hover {
        background-image: url(../images/instagram-blue.svg);
        transform: scale(1.2);
      }
    }
  }
  